import {Component, ElementRef, Input, OnInit, ViewChild, ViewChildren} from '@angular/core';
import * as Panolens from '../../../../node_modules/panolens/build/panolens.module.js';
import {CameraPosition} from '../../model/camera';


@Component({
  selector: 'app-panorama',
  templateUrl: './panorama.component.html',
  styleUrls: ['./panorama.component.scss']
})
export class PanoramaComponent implements OnInit {

    @ViewChild('pano') panoramaDiv: ElementRef;
    @Input() src: string;
    @Input() cameraPosition: CameraPosition;

    viewer;
    panorama;

    constructor() {

    }


    ngOnInit() {
        const options = {
            container: this.panoramaDiv.nativeElement,
            cameraFov: 70
        };
        this.viewer = new Panolens.Viewer(options);
        this.viewer.getCamera().position.set(this.cameraPosition.x, this.cameraPosition.y, this.cameraPosition.z)
        this.panorama =  new Panolens.ImagePanorama(this.src);
        this.viewer.add(this.panorama);
    }


    onClick($event: MouseEvent) {
        console.log('s')
        console.log(this.viewer.getCamera());
    }
}
